
'use server';

import Razorpay from 'razorpay';
import { z } from 'zod';

const OrderOptionsSchema = z.object({
  amount: z.number().min(1),
  currency: z.string().length(3),
});

type OrderOptions = z.infer<typeof OrderOptionsSchema>;

const razorpay = new Razorpay({
  key_id: process.env.RAZORPAY_KEY_ID!,
  key_secret: process.env.RAZORPAY_KEY_SECRET!,
});

export async function createOrder(options: OrderOptions) {
  const validatedOptions = OrderOptionsSchema.parse(options);
  
  const order = await razorpay.orders.create({
    amount: validatedOptions.amount,
    currency: validatedOptions.currency,
    receipt: `receipt_order_${new Date().getTime()}`,
  });

  if (!order) {
    throw new Error('Failed to create Razorpay order');
  }

  return order;
}
